<template>
	<form @submit="submit">
		<div class="form-row">
			<div class="form-group col">
				<label for="name">Nombre</label>
				<input type="text" v-model="name" class="form-control" id="name" aria-describedby="nameHelp" placeholder="Ingrese un nombre" required>
				<small id="nameHelp" class="form-text text-muted">Este nombre servir de identificacin para los usuarios.</small>
			</div>
			<div class="form-group col">
				<label for="company">Empresa</label>
				<input type="text" v-model="company" class="form-control" id="company" placeholder="Ingrese compana" required>
			</div>
			<div class="form-group col">
				<label for="name">Cdigo</label>
				<input type="text" v-model="code" class="form-control" id="code" aria-describedby="codeHelp" placeholder="Ingrese un cdigo" required>
				<small id="codeHelp" class="form-text text-muted">Esto supongo que servir para algo en algn momento...</small>
			</div>
		</div>
		<div class="form-row">
			<div class="form-group col">
				<label for="pay-endpoint">Endpoint de pago</label>
				<input type="url" v-model="paymentEndpoint" class="form-control" id="pay-endpoint"
				       aria-describedby="pay-endpointHelp" placeholder="Ingrese endpoint de pago" required>
				<small id="pay-endpointHelp" class="form-text text-muted">Esta url ser a la que se posteen los pagos</small>
			</div>
			<div class="form-group col">
				<label for="roll-endpoint">Endpoint de rollback</label>
				<input type="url" v-model="rollbackEndPoint" class="form-control" id="roll-endpoint" placeholder="Ingrese endpoint de rollback" required>
			</div>
		</div>
		<button style="float: right" type="submit" class="btn btn-primary">Submit</button>
	</form>
</template>

<script>
	import {mapActions} from "vuex";

	export default {
		name: "ProviderCreateForm",
		data(){
			return{
				name: '',
				company: '',
				code: '',
				paymentEndpoint: '',
				rollbackEndPoint: ''
			}
		},
		methods: {
			submit(e){
				e.preventDefault();
				const provider = {name: this.name, company: this.company, code: this.code,
					rollbackEndPoint: this.rollbackEndPoint, paymentEndpoint: this.paymentEndpoint};
				this.createProvider(provider);
			},
			...mapActions(["createProvider"])
		}
	}
</script>

